.main {
	display: flex;
	flex-direction: column;
	padding: 20px min(104px, 7%) 80px;
	background-color: white;
	text-align: center;
	justify-content: center;
}

.main_none {
	display: none;
}

.main__title {
	font-family: 'Roboto Slab', Arial, sans-serif;
	font-style: normal;
	font-weight: normal;
	font-size: 30px;
	line-height: 34px;
	color: #1A1B22;
	padding: 0;
	margin-top: 0;
	margin-bottom: 32px;
}

.main__showmore-button {
	background-color: #FFFFFF;
	border-radius: 80px;
	max-width: 288px;
	padding: 20px 84px;
	font-family: 'Roboto', Arial, sans-serif;
	font-style: normal;
	font-weight: 700;
	font-size: 18px;
	line-height: 24px;
	color: #1A1B22;
	cursor: pointer;
	margin: 64px auto 0;
	background-color: -internal-light-dark(rgb(239, 239, 239), rgb(59, 59, 59));
	border-radius: 0.3rem;
	border-width: 2px;
    border-style: outset;
    border-color: -internal-light-dark(rgb(118, 118, 118), rgb(133, 133, 133));
    border-image: initial;
}

.main__showmore-button:focus {
	outline: none;
}

.main__showmore-button:hover {
	opacity: 0.9
}

@media screen and (max-width: 960px) {
	.main {
		padding: 20px 5%;
	}
	.main__title {
		font-size: 30px;
		line-height: 34px;
	}
	.main__showmore-button {
		padding: 16px 60px;
		margin: 32px auto 0;
	}
}

@media screen and (max-width: 700px) {
	.main__showmore-button {
		display: flex;
		justify-content: center;
		align-items: center;
		max-width: 620px;
		width: 100%;
	}
}

@media screen and (max-width: 320px) {
	.main {
		padding: 32px 5%;
	}
	.main__title {
		margin-bottom: 24px;
	}
	.main__showmore-button {
		margin: 15px 0 0;
	}
}
