.server-error {
  background-color: #f5f6f7;
  position: relative;
  box-sizing: border-box;
  padding: 80px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.server-error__text {
  font-family: "Roboto", Arial, sans-serif;
  max-width: 342px;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 24px;
  text-align: center;
  color: #b6bcbf;
  margin: 0;
}
