.card {
  padding: 60px 50px;
}

.card__container {
  background-color: #fff;
  box-sizing: border-box;
  padding: 0 0;
  display: flex;
  justify-content: space-between;
}

.card__pic {
  display: flex;
  max-width: 380px;
  width: 100%;
  object-fit: contain;
  margin-right: 20px;
}

.card__details {
  width: calc(100% - 173px - 60px);
}

.card__categories {
  color: #b6bcbf;
}
.card__authors {
  color: #b6bcbf;
}

.card__title {
  font-size: 1.7rem;
  font-weight: 700;
  line-height: 1.7;
  margin-bottom: 20px;
  margin: 10px 0 0;
}

.card__subtitle {
  font-size: 1.1rem;
  font-weight: 600;
  line-height: 1.1;
  margin-bottom: 2px;
  margin: 10px 0 0;
}

@media screen and (max-width: 700px) {
  .card__pic {
    max-width: 250px;
  }
  .card__title {
    font-size: 1.1rem;
    font-weight: 600;
    line-height: 1.1;
  }
}

@media screen and (max-width: 600px) {
  .card__pic {
    max-width: 200px;
  }
  .card__title {
    font-size: 1.1rem;
    font-weight: 600;
    line-height: 1.1;
  }
}

@media screen and (max-width: 425px) {
  .card__container {
    flex-direction: column;
    justify-content: center;
  }
  .card__pic {
    margin: 0 auto;
  }

  .card__details {
    margin: 0 auto;
    width: 100%;
  }
}

.button {
  display: block;
  box-sizing: border-box;
  border: 2px solid #000;
  background-color: transparent;
  color: #000;
  cursor: pointer;
}

.button:hover {
  background-color: grey;
}

